import React, { useState } from "react";
import OtpInput from "react-otp-input";
import { Link } from "react-router-dom";

import ReactPhoneInput from "react-phone-input-2";
import './style.css';
import "react-phone-input-2/lib/style.css";
export default function Registration() {
    const [state, setState] = useState({
        phone: "",
        otp: ""
      });
    
      const setValue = (fieldName) => (evt) =>
        setState({ [fieldName]: evt.target.value });
    
      const handleChange = (otp) => setState({ otp });
      return (
        <div className="app">
        <div className="py-12 h-screen bg-gray-100 m-z ">
          <div className="max-w-lg mx-auto">
            <form className="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4 ">
              <div className="text-center">
                <h1 className="font-bold text-gray-800 text-2xl">
                  Please Verify your account
                </h1>
              </div>
              <div className="block  w-100">
                {}
                <div className="my-4">
                  <label
                    className="block text-gray-700 text-sm font-medium mb-4 text-lg text-center"
                    htmlFor="phone"
                  >
                    Enter your 10 digit number
                  </label>
                  <ReactPhoneInput
                    className="bg-white focus:outline-none focus:shadow-outline border border-gray-300 rounded-lg py-2 px-4 block w-full appearance-none leading-normal"
                    country={'in'}
                    containerStyle={{ width: "50%" ,
                    marginTop:"10px"
                  }}
                    searchClass="search-class"
                    searchStyle={{
                      margin: "50px",
                    
                      width: "96%",
                      height: "30px"
                    }}
                    enableSearchField
                    disableSearchIcon
                  />
                </div>
                <div className="my-4">
                  <h1
                    className="block text-gray-700 text-sm font-medium mb-2  text-lg text-center"
                    htmlFor="username"
                  >
                    Enter Your OTP here
                  </h1>
                  <OtpInput
                    value={state.otp}
                    className="otp-input bg-white mx-1 text-lg focus:outline-none focus:shadow-outline border border-gray-200 rounded-lg  block w-full appearance-none leading-normal"
                    onChange={handleChange}
                    numInputs={4}
                    separator={<span></span>}
                  />
                </div>
                <div className="m-6">
                  <label
                    className="block text-gray-700 text-sm font-bold mb-2   text-center"
                    htmlFor="username"
                  >
                    <span>
                      Click here ?&nbsp;
                      <Link to="/" className="text-blue-600 mb-5">
                        Resend OTP
                      </Link>
                    </span>
                  </label>
                </div>
    
                <Link to="/chattingApp/chatBody"><button className="submit btn-block w-full ml-10 my-5 px-4 py-3 py-1 text-lg text-gray-100 rounded-lg font-semibold bg-green-700 hover:text-white hover:bg-green-400 hover:border-transparent focus:outline-none focus:ring-2 focus:ring-green-800 focus:ring-offset-2">
                  Submit
                </button>
                </Link>
              </div>
            </form>
          </div>
        </div>
        </div>
      );
}
